@tailwind base;
@tailwind components;
@tailwind utilities;

body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu',
		'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	overflow-x: hidden;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

@layer components {
	.center {
		display: flex;
		justify-content: center;
		align-items: center;
	}
}

.centerBike {
	position: absolute;
	top: 50%;
	left: 50%;
	margin-top: 26px;
	margin-left: -12px;
}

.center-abs {
	position: absolute;
	top: 50%;
	left: 50%;
	margin-top: -50px;
	margin-left: -50px;
}

#loop {
	height: 100px;
	width: 100px;
	border: #bb5f27 solid 4px;
	border-radius: 200px;
}

#loop:before {
	background: linear-gradient(
		to left,
		rgba(187, 95, 39, 0) 0%,
		rgba(187, 95, 39, 1) 30%,
		rgba(187, 95, 39, 1) 70%,
		rgba(187, 95, 39, 0) 100%
	);
	content: '';
	display: block;
	height: 4px;
	left: -100px;
	position: relative;
	top: 100px;
	width: 300px;
}

#bike-wrapper {
	height: 108px;
	width: 108px;
	animation: drive 3s linear infinite;
}

#bike {
	height: 24px;
	width: 25px;
	background-image: url('https://s3-us-west-2.amazonaws.com/s.cdpn.io/133687/motorbike.png');
}

.alert-bar.show {
	transition: all 0.2s;
	transform: translateY(-45px);
}

@keyframes drive {
	0% {
		margin-left: -364px;
		opacity: 0;
	}
	33.33% {
		transform: rotate(0deg);
		margin-left: -50px;
		opacity: 1;
	}
	66.66% {
		transform: rotate(-360deg);
		margin-left: -50px;
		opacity: 1;
	}
	100% {
		margin-left: 264px;
		transform: rotate(-360deg);
		opacity: 0;
	}
}
